const INITIAL_STATE = {
  name: '',
  assertions: 0,
  score: 0,
  gravatarEmail: '',
};

function player(state = INITIAL_STATE, action) {
  switch (action.type) {
  case 'player':
    return {
      name: action.state.name,
      gravatarEmail: action.state.gravatarEmail,
      token: action.state.token.token,
      score: 0,
      assertions: state.assertions,
      question: action.state.question,
    };
  case 'button':
    return {
      ...state,
      button: action.button,
    };
  case 'timer':
    return {
      ...state,
      timer: action.state,
    };
  case 'score':
    return {
      ...state,
      score: action.payload + state.score,
      assertions: state.assertions + 1,
    };
  default:
    return state;
  }
}

if (window.Cypress) {
  window.player = player;
}

export default player;
