import React from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import newAction from '../redux/actions/index';
import './login.css';

class Login extends React.Component {
  constructor() {
    super();
    this.state = {
      name: '',
      button: true,
      validation: false,
    };
    this.validatioEmail = this.validatioEmail.bind(this);
  }

  componentDidUpdate(prevProps, prevState) {
    const { name, gravatarEmail } = this.state;
    if (prevState.name !== name) {
      this.validatioEmail();
    }
    if (prevState.gravatarEmail !== gravatarEmail) {
      this.validatioEmail();
    }
  }

  handlechange = ({ target }) => {
    const { name, value } = target;
    this.setState({
      [name]: value,
    });
  }

  enviainfo = () => {
    const { user, history } = this.props;
    user(this.state);
    history.push('/game');
  }

  validatioEmail() {
    const numer = 3;
    const { gravatarEmail, name, validation } = this.state;
    const valid = /\S+@\S+\.\S+/;
    this.setState({
      validation: valid.test(gravatarEmail),
    });

    if (name.length >= numer && validation) {
      this.setState({
        button: false,
      });
    }
    if (name.length < numer || validation === false) {
      this.setState({
        button: true,
      });
    }
  }

  render() {
    const { history } = this.props;
    const { button, gravatarEmail, name } = this.state;
    return (
      <div className="login-container">
        <div className="login efeito-vidro">
          <input
            type="text"
            data-testid="input-player-name"
            placeholder="Nome"
            value={ name }
            name="name"
            onChange={ this.handlechange }
          />
          <input
            type="text"
            data-testid="input-gravatar-email"
            placeholder="E-mail"
            onChange={ this.handlechange }
            value={ gravatarEmail }
            name="gravatarEmail"
          />
          <button
            className="play-config-buttons"
            type="button"
            data-testid="btn-play"
            disabled={ button }
            onClick={ this.enviainfo }
          >
            Play
          </button>
          <button
            className="play-config-buttons"
            onClick={ () => history.push('/settings') }
            data-testid="btn-settings"
            type="button"
          >
            Configura√ß√µes
          </button>
        </div>
      </div>
    );
  }
}

Login.propTypes = {
  user: PropTypes.func.isRequired,
  history: PropTypes.shape({
    push: PropTypes.func.isRequired,
  }).isRequired,
};

const mapDispatchToProps = (dispatch) => ({
  user: (state) => dispatch(newAction(state)),
});

export default connect(null, mapDispatchToProps)(Login);
