import React, { Component } from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import './feedback.css';

class Feedback extends Component {
  render() {
    const { name, score, assertions, history } = this.props;
    const number = 3;
    return (
      <div className="main-feedback">
        <header className="feedback-header">
          <h2 data-testid="header-player-name">{name}</h2>
          <h2 data-testid="feedback-total-score">{score}</h2>
          <h2 data-testid="feedback-total-question">{assertions}</h2>
          <img
            data-testid="header-profile-picture"
            src="https://www.gravatar.com/avatar/c19ad9dbaf91c5533605fbf985177ccc"
            alt="foto de perfil"
          />
        </header>
        <div className="container-led feedback">
          <h2 hidden data-testid="header-score">{score}</h2>
          <h2
            className={ assertions < number ? 'red-feedback' : 'blue-feedback' }
            data-testid="feedback-text"
          >
            {assertions < number ? 'Could be better... üò¢' : 'Well Done! ü•≥'}

          </h2>
        </div>
        <div className="botoes-feedback">
          <button
            className="btn-feedback"
            data-testid="btn-play-again"
            type="button"
            onClick={ () => history.push('/') }
          >
            Play Again

          </button>
          <button
            className="btn-feedback"
            data-testid="btn-ranking"
            type="button"
            onClick={ () => history.push('/ranking') }
          >
            Ranking

          </button>
        </div>
      </div>
    );
  }
}

Feedback.propTypes = {
  name: PropTypes.string.isRequired,
  score: PropTypes.number.isRequired,
  assertions: PropTypes.number.isRequired,
  history: PropTypes.shape({
    push: PropTypes.func.isRequired,
  }).isRequired,
};

const mapStateToProps = (state) => ({
  name: state.player.name,
  score: state.player.score,
  assertions: state.player.assertions,
});

export default connect(mapStateToProps)(Feedback);
