import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import './ranking.css';

class Ranking extends Component {
  render() {
    const { history } = this.props;
    const rankingData = JSON.parse(localStorage.getItem('ranking'));
    if (rankingData !== null) {
      rankingData.sort((a, b) => {
        const CODENUMBER = -1;
        if (a.score > b.score) return CODENUMBER;
        return true;
      });
    }
    return (
      <div className='centraliza-raking'>
        <h1 data-testid="ranking-title">Ranking</h1>
        <table className='centraliza-raking-result'>
          <thead>
            <tr className="ranking-header">
              <th>Foto de perfil</th>
              <th>Nome</th>
              <th>Score</th>
            </tr>
          </thead>
          <tbody>
            {rankingData?.map((e, index) => (
              <tr key={ index }>
                <td>
                  <img
                    data-testid="header-profile-picture"
                    src="https://www.gravatar.com/avatar/c19ad9dbaf91c5533605fbf985177ccc"
                    alt="foto de perfil"
                  />

                </td>
                <td data-testid={ `player-name-${index}` }><h3>{e.name}</h3></td>
                <td data-testid={ `player-score-${index}` }><h3>{e.score}</h3></td>
              </tr>
            ))}
          </tbody>
        </table>
        <button
          className='ranking-btn'
          data-testid="btn-go-home"
          type="button"
          onClick={ () => history.push('/') }
        >
          login

        </button>
      </div>
    );
  }
}

Ranking.propTypes = {
  history: PropTypes.shape({
    push: PropTypes.func.isRequired,
  }).isRequired,
};

const mapStateToProps = (state) => ({
  name: state.player.name,
  score: state.player,
});

export default connect(mapStateToProps)(Ranking);
