import PropTypes from 'prop-types';
import React, { Component } from 'react';
import { connect } from 'react-redux';
import '../App.css';
import Header from '../components/Header';
import { score } from '../redux/actions/index';
import './game.css';

let c = 0; // current
let randomAlternatives;

class Game extends Component {
  constructor() {
    super();
    this.state = {
      button: false,
      count: 30,
      nextButton: false,
      randomAlt: true,
      teste: 0,
    };
    this.disabledbutton = this.disabledbutton.bind(this);
  }

  componentDidMount() {
    this.makeTimer();
  }

  componentDidUpdate(prevProps, prevState) {
    const { token, history } = this.props;
    if (prevState.count === 1) {
      clearInterval(this.time);
      this.disabledbutton();
    }
    if (token) {
      localStorage.setItem('token', token.token);
    }

    if (token.question.response_code !== 0) {
      history.push('/');
    }
  }

  random = (array) => {
    const noMagicNumber = 0.5;
    return array.sort(() => Math.random() - noMagicNumber);
  };

  nextQuestion = () => {
    const { history, token } = this.props;
    const MAX_QUESTIONS = 4;
    this.makeTimer();
    this.setState({ nextButton: false, button: false, count: 30 });
    if (c === MAX_QUESTIONS) {
      if (!localStorage.getItem('ranking')) {
        localStorage.setItem('ranking', '[]');
      }
      const rankingData = JSON.parse(localStorage.getItem('ranking'));
      const playerData = [...rankingData, { name: token.name, score: token.score }];
      localStorage.setItem('ranking', JSON.stringify(playerData));
      history.push('/feedback');
      c = 0;
    } else {
      c += 1;
      this.setState({ randomAlt: true });
    }
  }

  handleAnswer = ({ target }) => {
    const { results } = this.props;
    const correct = results.map((obj) => obj.correct_answer)[c];
    const dificult = results.map((obj) => obj.difficulty)[c];
    const buttons = target.parentElement.children;
    clearInterval(this.time);
    this.setState({ nextButton: true });
    Array.from(buttons).map((e) => {
      if (e.innerHTML === correct) {
        e.className = 'green-border';
        if (target.innerHTML === correct) this.calculo(this.dificult(dificult));
      } else {
        e.className = 'red-border';
      }
      return e;
    });
  }

  makeTimer = () => {
    const { count } = this.state;
    const second = 1000;
    this.time = setInterval(() => {
      if (count > 0) {
        this.setState((PrevState) => ({
          count: PrevState.count - 1,
        }));
      }
    }, second);
  }

  dificult = (dificult) => {
    const HARD = 3;
    if (dificult === 'easy') return 1;
    if (dificult === 'medium') return 2;
    if (dificult === 'hard') return HARD;
  }

  calculo = (dificult) => {
    const { sumToScore } = this.props;
    const { count } = this.state;
    const Magic = 10;
    if (count !== undefined) {
      const soma = Magic + (count * dificult);
      console.log(soma);
      this.setState(({
        teste: soma,
      }), () => {
        const { teste } = this.state;
        sumToScore(teste);
      });
    }
  }

  disabledbutton() {
    this.setState({
      button: true,
      nextButton: true,
    });
  }

  render() {
    const { results } = this.props;
    const { button, nextButton, randomAlt, count } = this.state;
    if (results?.length > 0) {
      const all = results.map((obj) => [...obj.incorrect_answers, obj.correct_answer])[c];

      const corre√ß√£o = results.map((obj) => obj.correct_answer)[c];
      const alternatives = all.map((a, index) => ({
        answer: a,
        datatest: a === corre√ß√£o ? 'correct-answer' : `wrong-answer-${index}`,
      }));
      if (randomAlt) {
        randomAlternatives = this.random(alternatives);
        this.setState({ randomAlt: false });
      }

      return (
        <div>
          <Header />
          <hr />
          <div className="main-container">
            {results.map(({ category, question }, index) => (
              <div key={ index } className="game-container">
                <h2 className="centraliza">{count}</h2>
                <h2 data-testid="question-category">{category}</h2>
                <p data-testid="question-text">{question}</p>
                <div data-testid="answer-options">
                  {randomAlternatives?.map(({ answer, datatest }, key) => (
                    <button
                      className="alternatives-buttons"
                      value={ answer }
                      key={ key }
                      type="button"
                      data-testid={ datatest }
                      onClick={ this.handleAnswer }
                      disabled={ button }
                    >
                      {answer}
                    </button>
                  ))}
                </div>
                {nextButton && (
                  <button
                    className="alternatives-buttons"
                    type="button"
                    onClick={ this.nextQuestion }
                    data-testid="btn-next"
                  >
                    Next
                  </button>)}
              </div>
            ))[c]}
          </div>
        </div>
      );
    }

    return (
      <div className="loading-container">
        <h1>Loading...</h1>
      </div>
    );
  }
}

Game.propTypes = {
  token: PropTypes.string.isRequired,
  history: PropTypes.shape({
    push: PropTypes.func.isRequired,
  }).isRequired,
  results: PropTypes.arrayOf(PropTypes.object).isRequired,
  sumToScore: PropTypes.func.isRequired,
};

const mapDispatchToProps = (dispatch) => ({
  sumToScore: (payload) => dispatch(score(payload)),
});

const mapStateToProps = (state) => ({
  token: state.player,
  results: state.player.question?.results,
});

export default connect(mapStateToProps, mapDispatchToProps)(Game);
