const PageLogin = 'player';

function user(json) {
  return { type: PageLogin, state: json };
}

function newAction(payload) {
  return async (dispatch) => {
    const request = await fetch('https://opentdb.com/api_token.php?command=request');
    const data = await request.json();
    payload.token = data;
    const requestQ = await fetch(`https://opentdb.com/api.php?amount=5&token=${data.token}`);
    const data1 = await requestQ.json();
    payload.question = data1;
    dispatch(user(payload));
  };
}

export function timer(play) {
  return { type: 'timer', state: play };
}

// action que incrementa valor √† score se o usu√°rio acertar
export function score(pontos) {
  return { type: 'score', payload: pontos };
}
export function button(state) {
  return { type: 'button', button: state };
}

export default newAction;
